@import url(https://cdnjs.cloudflare.com/ajax/libs/font-awesome/5.13.0/css/all.min.css);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
  height: 100vh;
  overflow: auto;
}
*{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
.heading {
  padding-top: 1rem;
}
.dark-mode {
  background: #282c34;
  color: rgb(235, 235, 235);
}
.header{
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1em;

}

.checkbox {
  opacity: 0;
  position: relative;
}

.label {
  width: 50px;
  height: 26px;
  background-color:#111;
  display: flex;
  border-radius:50px;
  align-items: center;
  justify-content: space-between;
  padding: 5px;
  position: relative;
  transform: scale(1.5);
}

.ball {
  width: 20px;
  height: 20px;
  background-color: white;
  position: absolute;
  top: 2px;
  left: 2px;
  border-radius: 50%;
  transition: transform 0.2s linear;
}

/*  target the elemenent after the label*/
.checkbox:checked + .label .ball{
  transform: translateX(24px);
}

.fa-moon {
  color: pink;
}

.fa-sun {
  color: yellow;
}
@media (max-width: 768px) {
    .header{
      flex-direction: column;
    
    }
}
.card-list{display:flex;flex-wrap:wrap;justify-content:center;padding:20px}

.card{background-color:#fff;border-radius:5px;padding:2rem;box-shadow:0 0.5rem 1rem rgba(0,0,0,0.3);margin:1rem;transition:0.3s}.card:hover{cursor:pointer;box-shadow:0 0.9rem 1.75rem rgba(0,0,0,0.3)}.card__title{font-size:1rem}.card__contact{font-size:0.8rem;color:#999}.card__image{width:80px;height:80px;border-radius:50%;border:1px solid #e91f1f}.dark-mode .card{background:#282c34;color:#fff;border:0;box-shadow:2px 2px 10px 2px #716f7e}

.search{font-size:1.5rem;padding:0.75rem;margin:1rem 0;border-radius:5px}.dark-mode .search{background:#282c34;color:#fff;border:0;box-shadow:2px 2px 10px 2px #716f7e}

